.btn {
  display: inline-flex;
  justify-content: center;
  padding: 20px;
  width: fit-content;
  height: fit-content;
  font-family: $ff-roboto;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  color: $color-text-white;
  text-transform: uppercase;
  background-color: transparent;
  cursor: pointer;
  user-select: none;
  transition: opacity $trans-default;
  border: 3px solid $color-text-white;


  &:disabled {
    opacity: 0.5;
    pointer-events: none;
  }

  @include focus {
    border: none;
    outline: 2px solid $color-default-white;
  }
}
