.logo {
  @include hover {
    opacity: 0.8;
  }

  @include focus {
    border: none;
    outline: 2px solid $color-default-white;
  }
}

.logo__img {
  width: 122px;
  height: 22px;

  @include vp-1199 {
    width: 82px;
    height: 17px;
  }

  @include vp-767 {
    width: 69px;
    height: 24px;
  }
}

.logo__img--footer {
  width: 88px;
  height: 32px;

  @include vp-1199 {
    width: 82px;
    height: 18px;
  }

  @include vp-767 {
    width: 68px;
    height: 15px;
  }
}
